body {
  background: #7b4397;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #a29cdc, #7b4397);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#a29cdc), to(#7b4397));
  background: -webkit-linear-gradient(right, #a29cdc, #7b4397);
  background: -o-linear-gradient(right, #a29cdc, #7b4397);
  background: linear-gradient(to left, #a29cdc, #7b4397);
  font-family: 'Roboto', sans-serif;
  color: white;
  text-shadow: 2px 2px 2px black;
  text-align: center;
  line-height: 1.5;
}

h1,
h1 a {
  font-size: 60px;
}

a {
  text-decoration: none;
  color: white;
  text-shadow: 2px 2px 2px black;
}

.details_slides, .twitter_handle {
  display: block;
}

.click_me {
  display: block;
  padding-top: 10px;
  -webkit-animation: bounce_click .4s ease-in-out infinite;
          animation: bounce_click .4s ease-in-out infinite;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
}

.loader h2 {
  text-transform: uppercase;
  padding-top: 50px;
}

.loader img {
  max-width: 100%;
  width: 200px;
  -webkit-animation: bounce .7s ease-in-out infinite;
          animation: bounce .7s ease-in-out infinite;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
}

.message {
  font-size: 18px;
  width: 50%;
  margin: 0 auto;
}


.talk-container{
  display: grid;
  grid-gap: 40px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  padding: 20px;
  width: auto;
}


.back {
  padding: 20px;
  border: 0;
  height: auto;
  width: auto;
  background-color: #e0b606;
  font-weight: 500;
  font-size: 18px;
  text-transform: uppercase;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transition: background .2s ease-in, -webkit-transform .2s ease-in-out;
  transition: background .2s ease-in, -webkit-transform .2s ease-in-out;
  -o-transition: background .2s ease-in, transform .2s ease-in-out;
  transition: background .2s ease-in, transform .2s ease-in-out;
  transition: background .2s ease-in, transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
  margin-bottom: 50px;
}

.back:hover {
  background-color: #f4c70a;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: background .2s ease-in, -webkit-transform .2s ease-in-out;
  transition: background .2s ease-in, -webkit-transform .2s ease-in-out;
  -o-transition: background .2s ease-in, transform .2s ease-in-out;
  transition: background .2s ease-in, transform .2s ease-in-out;
  transition: background .2s ease-in, transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
  cursor: pointer;
}

.back a {
  color: black;
  text-shadow: none;
  padding: 40px;
}
.card_title {
  font-size: 30px;
}

.cards {
  padding: 20px 0;
}

.card_image {
  border-radius: 50%;
  max-width: 100%;
  height: 150px;
  border: 2px solid black;
  -webkit-transition: .2s ease-in;
  -o-transition: .2s ease-in;
  transition: .2s ease-in;
}

.card_image:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: .2s ease-out;
  -o-transition: .2s ease-out;
  transition: .2s ease-out;
}

.twitter, .slides, .details {
  display: block;
  font-size: 24px;
  padding: 16px 0;
  margin: 0 auto;
  width: auto;
}

.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 250ms ease-in;
  -o-transition: opacity 250ms ease-in;
  transition: opacity 250ms ease-in;
}

.headline {
  font-size: 24px;
}

.learning {
  width: 80%;
  margin: 0 auto;
  padding: 40px;
}

.learning h3 {
  font-size: 36px;
}

.summary {
  width: 70%;
  margin: 0 auto;
}

@-webkit-keyframes bounce_click {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  to {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
}

@keyframes bounce_click {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  to {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
}

@-webkit-keyframes bounce {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  to {
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
}

@keyframes bounce {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  to {
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
}

@media (max-width: 767px) {

  h1, h1 a {
    font-size: 40px;
  }

  .message {
    width: 80%;
  }

  .talk-container {
    padding: 0;
  }

  .twitter, .slides, .details {
    font-size: 18px;
  }

  .learning {
    padding: 20px;
  }
}


